/* Layout */


/* Main content */
article {
  max-width: 100%;
  padding-bottom: 3rem;
  main > & {
    margin-top: 1rem;
    margin-top: var(--body-text-spacer);
  }

  > * {
    margin-left: auto;
    margin-right: auto;
    margin-left: var(--margin-left);
    margin-right: var(--margin-right);
  }
}

